/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/

html {
  box-sizing: border-box;
  @include font-size(1);
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: $color__background-body;
  /* Fallback for when there is no custom background color defined. */
}

blockquote,
q {
  quotes: '''';

  &:before,
  &:after {
    content: '';
  }
}

hr {
  background-color: $color__background-hr;
  border: 0;
  height: 1px;
  margin-bottom: $base__space-unit;
}

ul,
ol {
  // margin: 0 0 $base__space-unit ($base__space-unit * 2);
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

ol {
  list-style: decimal;
}

li>ul,
li>ol {
  margin-bottom: 0;
  margin-left: $base__space-unit;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 $base__space-unit $base__space-unit;
}

article {
  margin-top: 45px;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  width: 100%;
  /* Adhere to container width. */
}

table {
  margin: 0 0 $base__space-unit;
  width: 100%;
}

a {
  text-decoration: none;
}

i {
  padding-right: $base__space-unit/3;
}

aside>h2 {
  font-size: $font__size * 1.5;
}

span {
  font-family: $font__accent;
  text-transform: uppercase;
}